import { defineComponent as _defineComponent } from 'vue'
import {
  onMounted,
  provide,
  ref,
  watch
} from "vue";
import Navigation from './Navigation.vue';
import VMain from './Main.vue';
import { $store_get_all } from "@/utils/store";
import sidebar4 from '@/assets/sidebar/sidebar-4.jpeg';

/* 定义一些变量 */

export default _defineComponent({
  expose: [],
  setup(__props) {

const hour = ref<number>(new Date().getHours());
const bgColor = ref("#fff");
const color = ref("#333");
provide("$bg", bgColor);
provide("$color", color);

// 每小时改变一次hour的值
setInterval(() => (hour.value = new Date().getHours()), 1000 * 3600);
// 监听hour的值
watch(
    hour,
    (v) => {
      // 是否是晚上: 小于早6点 大于晚20点
      const isDark = v >= 20 || v < 6;
      isDark ? (bgColor.value = "#333") : (bgColor.value = "#fff");
      isDark ? (color.value = "#fff") : (color.value = "#333");
    },
    {immediate: true}
);

const setSetting = (v) => {
  const {menuBg: bg = transparentMenuBg.value, menuImg: img = menuImg.value} = v;
  transparentMenuBg.value = bg;
  menuImg.value = img;
};

const menuExpand = ref(true);
const menuImg = ref(sidebar4);
const transparentMenuBg = ref(false);

provide('expand', menuExpand);
provide('img', menuImg);
provide('transparentMenuBg', transparentMenuBg);

provide('setExpand', (v) => menuExpand.value = v);
provide('setImg', (v) => menuImg.value = v);
provide('setTransparentMenuBg', (v) => transparentMenuBg.value = v);
provide('setSetting', setSetting);

onMounted(() => {
  const json = $store_get_all();
  setSetting(json);
})


return { hour, bgColor, color, setSetting, menuExpand, menuImg, transparentMenuBg, onMounted, provide, ref, watch, Navigation, VMain, $store_get_all, sidebar4 }
}

})