
import { defineComponent, computed } from "vue";
import Icon from "@/components/Icon.vue";
export default defineComponent({
  name: "Message",
  components: {
    "v-icon": Icon,
  },
  props: {
    type: {
      type: String,
      default: "info",
      validator: (v: string) => ["success", "error", "warning", "info"].indexOf(v) !== -1,
    },
    lastTime: {
      type: Number,
      default: 2500,
    },
    content: {
      type: String,
      defualt: "这是一条提示信息",
    },
    isShow: {
      type: Boolean,
      default: false,
    },
    isLeave: {
      type: Boolean,
      defualt: false,
    },
    isEnter: {
      type: Boolean,
      default: false,
    },
    seed: {
      type: Number,
      default: 0,
    },
    isDestory: {
      type: Boolean,
      defualt: false,
    },
  },
  setup(props) {
    const transition = (matchup: any) => matchup[props.type];

    const iconSymbol = computed(() => {
      return transition({
        success: "icon-chenggong",
        error: "icon-shibai",
        warning: "icon-xianshi_jinggaotianchong",
        info: "icon-xinxi3",
      });
    });
    const textColor = computed(() => {
      return transition({
        success: "#3BC965",
        error: "#de3c3c",
        warning: "#eea832",
        info: "#909399",
      });
    });

    const containerStyle = computed(() => ({ top: `${props.seed * 70}px` }));
    const containerClass = computed(() => {
      const classes = ["message-container", 'v-blur', props.type];
      if (props.isShow) classes.push("isShow");
      if (!props.isShow) classes.push("hide");
      if (props.isEnter) classes.push("enter");
      if (props.isLeave) classes.push("leave");
      return classes;
    });
    return {
      iconSymbol,
      textColor,
      containerStyle,
      containerClass,
    };
  },
});
