import * as directives from '@/directive';
import { App } from 'vue';

export default {
  install(app: App, options: any) {
    // 注册自定义指令
    for (let name in directives) {
      // @ts-ignore
      const directive = directives[name]; // 这里取出来的name是大写的, 但是好像不区分大小写
      app.directive(name, directive);
    }
  }
}
