import { defineComponent as _defineComponent } from 'vue'
import {computed, inject, ref, watch} from "vue";
import VMenu from '@/components/navigation/Menu.vue';
import logo1 from "@/assets/logo/logo.png";
import logo2 from '@/assets/logo/logo2.png';

export default _defineComponent({
  expose: [],
  setup(__props) {

const transparentMenuBg = inject('transparentMenuBg');
const bgImg = inject('img');
const expand = inject('expand');
const logo = ref(logo1);

const txtColor = computed(() => transparentMenuBg.value ? '#333' : '#eee');
watch(() => expand.value, (v) => v ? (logo.value = logo1) : (logo.value = logo2));


return { transparentMenuBg, bgImg, expand, logo, txtColor, computed, inject, ref, watch, VMenu, logo1, logo2 }
}

})